import config from './loan-a-list.config.json'
import JsonViewer from '@/components/JsonViewer'
import moment from 'moment'
import VueNumeric from 'vue-numeric'
export default {
    name: 'loan-a-list',
    data() {
        return {
            config,
            employeeId: '',
            errorMessage: '',
            loans: []
        }
    },
    components: {
        JsonViewer,
        VueNumeric
    },
    methods: {
        formatDate(_date) {
            return _date !== '' ? moment(_date).format('DD-MMM-YYYY') : '-'
        },
        getLoans() {
            this.$http({
                method: 'get',
                url: `/loan/view/${this.$store.getters.getUid}/loans-a`,
                headers: {
                    Authorization: 'Bearer ' + this.$store.getters.getToken
                }
            })
            .then(response => {
                this.loans = response.data
            })
            .catch(error => {
                this.errorMessage = error.response.data.response
            })
        },
        getStatus(_status) {
            let status = ''
            switch (_status) {
                case 's':
                    status = 'SUBMITTED'
                    break
                case 'a':
                    status = 'APPROVED'
                    break
                case 'r':
                    status = 'REJECTED'
                    break
                case 'd':
                    status = 'DISBURSED'
                    break
                default:
                    status = ''
            }
            return status
        }
    },
    created() {
        this.getLoans()
    }
}